import React, { Fragment, useState, useEffect, useContext } from 'react';
import { User, LogOut } from 'react-feather';
import {firebase_app} from "../../../data/config";
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import {EditProfile} from '../../../constant'
import {getProfilePictureFromDatabase} from '../../../services/headerMenu.service';
import { UserContext} from '../../../auth/context/userContext';

const UserMenu = ({ history }) => {

    
    const [logo, setLogo] = useState('');
    const { userID } = useContext(UserContext);
    
    useEffect(() => {
        const profilePicture = async () => {
            const data = await getProfilePictureFromDatabase(userID);
            setLogo(data)
         }
       profilePicture()
   
    }, [userID])

    
    const Logout_From_Firebase = () => {
        localStorage.removeItem('profileURL')
        localStorage.removeItem('userID')
        firebase_app.auth().signOut()

        history.push(`${process.env.PUBLIC_URL}/login`)
    }

    

    return (
        <Fragment>
            <li className="onhover-dropdown">
                <div className="media align-items-center">
                    <img className="align-self-center pull-right img-50 rounded-circle blur-up lazyloaded" src={logo} alt="header-user" />
                    <div className="dotted-animation">
                        <span className="animate-circle"></span>
                        <span className="main-circle"></span>
                    </div>
                </div>
                <ul className="profile-dropdown onhover-show-div p-20 profile-dropdown-hover">
                    <li><Link to={`${process.env.PUBLIC_URL}/profil`}><User />{EditProfile}</Link></li>
                    <li><a onClick={Logout_From_Firebase} href="#javascript" ><LogOut /> {"Log ud"}</a></li>
                </ul>
            </li>
        </Fragment>
    );
};


export default withRouter(UserMenu);